.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  background: #f8f9fa;
  min-height: 100vh;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.search-bar {
  width: 100%;
  max-width: 400px;
  padding: 10px 15px;
  font-size: 16px;
  margin: 0 auto 30px auto;
  display: block;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.pokemon-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(140px,1fr));
  gap: 20px;
}

.pokemon-card {
  background-color: white;
  border-radius: 12px;
  padding: 15px;
  cursor: pointer;
  border: 2px solid transparent;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pokemon-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0,0,0,0.15);
}

.glow {
  border-color: #ffcb05;
  box-shadow: 0 0 10px 3px #ffcb05;
}

.pokemon-sprite {
  width: 96px;
  height: 96px;
  margin-bottom: 8px;
}

.sprite-placeholder {
  width: 96px;
  height: 96px;
  background-color: #ddd;
  color: #999;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 40px;
  margin-bottom: 8px;
}

.pokemon-name {
  font-weight: bold;
  font-size: 16px;
  text-align: center;
  margin: 0;
  color: #333;
}

.pokemon-id {
  font-size: 14px;
  color: #666;
  margin-top: 4px;
}

.pokemon-info {
  margin-top: 40px;
  background: white;
  border-radius: 12px;
  padding: 20px;
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 4px 15px rgba(0,0,0,0.1);
  text-align: center;
}

.pokemon-info img {
  width: 150px;
  height: 150px;
  margin-bottom: 15px;
}

.pokemon-info h2 {
  margin-bottom: 10px;
}

.pokemon-info p {
  margin: 6px 0;
  font-size: 16px;
  color: #444;
}
